import Vue from 'vue'
import App from './App'
import router from './router'
import "vueify/lib/insert-css" // required for .vue file <style> tags
import InputTag from 'vue-input-tag'
import VueFlashMessage from 'vue-flash-message'
import VueClipboard from 'vue-clipboard2'
import VueMeta from 'vue-meta'
import VueGtag from 'vue-gtag'
import { Bartender } from './src/App'

Vue.config.productionTip = false

Vue.component('input-tag', InputTag)
Vue.use(VueFlashMessage)
Vue.use(VueClipboard)
Vue.use(VueMeta, { refreshOnceOnNavigation: true })
Vue.use(VueGtag, { config: { id: "UA-162115693-1" },
  pageTrackerTemplate(to, from) {
    if (to.name == 'Bartender') {
      return {
        page_title: Bartender.decode(to.params.encodedBartender).name,
        page_path: to.path
      }
    } else {
      return {
        page_title: to.name,
        page_path: to.path
      }
    }
  }
}, router);

Vue.directive('focus', {
  inserted: function (el) {
    var focusEl = (el.tagName == 'INPUT' || el.tagName == 'TEXTAREA') ? el : el.getElementsByTagName('INPUT')[0]
    var curPos = el.value ? el.value.length + 1 : 1
    focusEl.setSelectionRange(curPos, curPos)
    focusEl.focus()
  }
})

router.beforeEach((to, from, next) => {
  Vue.prototype.$flashStorage.destroyAll()
  
  var body = document.getElementsByTagName('BODY')[0];
  body.className = ''
  body.classList.add(to.meta.pageClass)

  next()
});


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    render: h => h(App)
})
